@import "variables.scss";

.search-and-notification {
  @include flex(space-between, center, row);
  width: 100%;
  margin-bottom: 15px;
  .hamburger-menu {
    @include size(50px, 50px);
    display: none;

    z-index: 11111;
    .hamburger-line {
      @include size(30px, 2px);
      margin: 3px;
      background-color: $black;
      border-radius: 30px;
    }
  }
  .search {
    width: 100%;
    border-radius: 100px;
    background-color: $white;
    @include flex(initial, center, row);
    padding: 13px 16px;

    input {
      outline: none;
      border: none;
      padding-left: 12px;
      width: 100%;
    }
  }
  .notification-profile {
    @include flex(space-around, initial, row);
    width: 12%;
    .notification {
      z-index: 1;
      @include flex(center, center, row);
      position: relative;
      cursor: pointer;
      .number {
        border: 3px solid $white;
        border-radius: 100%;
        @include flex(center, center, row);
        position: absolute;
        left: 18px;
        top: -3px;
        span {
          border-radius: 100%;
          background-color: $red;
          @include size (20px, 20px);
          display: inline-block;
          text-align: center;
          color: $white;
        }
      }
    }
    .profile {
      @include size (40px, 40px);
      background-image: url(../assets/circle.png);
      background-repeat: no-repeat;
      background-position: center;
      cursor: pointer;
    }
  }
}
