@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
* {
  box-sizing: border-box;
  font-family: "Lato", "Calibri", sans-serif;
}

html {
  font-size: 16px;
}



.App{max-width:1440px;margin:0 auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.App body{background:#f6f8fb;margin:0;padding:0}.App body ul{list-style:none;text-decoration:none;margin:0;padding:0}.App body svg{cursor:pointer !important}.App body h2{font-weight:700;font-size:2.5rem;line-height:2.875rem;letter-spacing:.2px}.App body h5{font-weight:700;font-size:1.25rem;line-height:1.625rem;letter-spacing:.2px}
.Sidebar{width:20%;height:100vh;background-color:#fff;box-shadow:4px 0px 16px rgba(16,30,115,.08);position:-webkit-sticky;position:sticky;top:0}.Sidebar .logo{padding:24px}.Sidebar .logo img:last-child{display:none}.Sidebar .search{background-color:#fff;padding:24px;display:none}.Sidebar .search input{outline:none;border:none;padding-left:12px;width:70%}.Sidebar p{font-weight:400;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;color:#a0a4a8;padding:14px 24px}.Sidebar li{padding:14px 5px;width:100%}.Sidebar li:hover svg path{fill:#558eff}.Sidebar li img{object-fit:contain}.Sidebar li a{text-decoration:none;font-weight:700;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;color:#52575c;margin-left:20px}.Sidebar li a:hover{color:#558eff}.Sidebar li a span{margin:10px}.Sidebar li:last-child{border-top:1px solid #e8e8e8;margin-top:22px;padding-top:22px}
.search-and-notification{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;width:100%;margin-bottom:15px}.search-and-notification .hamburger-menu{width:50px;height:50px;display:none;z-index:11111}.search-and-notification .hamburger-menu .hamburger-line{width:30px;height:2px;margin:3px;background-color:#25282b;border-radius:30px}.search-and-notification .search{width:100%;border-radius:100px;background-color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:initial;justify-content:initial;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;padding:13px 16px}.search-and-notification .search input{outline:none;border:none;padding-left:12px;width:100%}.search-and-notification .notification-profile{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:initial;align-items:initial;-webkit-flex-direction:row;flex-direction:row;width:12%}.search-and-notification .notification-profile .notification{z-index:1;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;position:relative;cursor:pointer}.search-and-notification .notification-profile .notification .number{border:3px solid #fff;border-radius:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;position:absolute;left:18px;top:-3px}.search-and-notification .notification-profile .notification .number span{border-radius:100%;background-color:#ff6760;width:20px;height:20px;display:inline-block;text-align:center;color:#fff}.search-and-notification .notification-profile .profile{width:40px;height:40px;background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-position:center;cursor:pointer}
.main{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:90%;padding:20px}
.cards{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:initial;align-items:initial;-webkit-flex-direction:row;flex-direction:row;margin:15px 0}.cards .card-lists{display:-webkit-flex;display:flex;-webkit-justify-content:initial;justify-content:initial;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;background-color:#fff;padding-left:24px;width:22%;box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px}.cards .card-lists .info-and-number{margin-left:24px}.cards .card-lists .info-and-number p:first-child{font-weight:700;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;text-align:left}.cards .card-lists .info-and-number p:last-child{font-weight:400;font-size:1.125rem;line-height:1.25rem;letter-spacing:.1px;text-align:left;color:#336cfb}
.big-chart{box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px;margin:15px 0;background-color:#fff}.big-chart .apexcharts-legend.apexcharts-align-right .apexcharts-legend-series,.big-chart .apexcharts-legend.apexcharts-align-left .apexcharts-legend-serie{display:-webkit-flex !important;display:flex !important}.big-chart .arrow_box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;position:relative;background:#fff;border:1px solid #fff;box-shadow:0px 0px 8px rgba(16,30,115,.12);width:100px;height:32px}
.little-charts-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin:15px 0}.little-charts-container .little-chart{box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px;background-color:#fff;margin:15px 0;width:49%}.little-charts-container .little-chart .arrow_box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;position:relative;background:#fff;border:1px solid #fff;box-shadow:0px 0px 8px rgba(16,30,115,.12);width:100px;height:32px}.apexcharts-xaxis-label{font-weight:700;font-size:.625rem;line-height:.75rem;letter-spacing:.2px;text-align:center}
.table{background-color:#fff;box-shadow:0px 12px 26px rgba(16,30,115,.06);border-radius:8px}.table h5{margin:24px}.table table{width:100%;text-align:left;border-spacing:0}.table table thead tr{background-color:rgba(232,232,232,.2)}.table table thead tr th{font-weight:700;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;padding:20px;opacity:1}.table table tbody tr td{padding:15px;font-weight:400;font-size:.875rem;line-height:1.25rem;letter-spacing:.1px;border-top:1px solid #e8e8e8}.table table tbody tr td:last-child .image-name img{cursor:pointer}.table table tbody tr td .image-name{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.table table tbody tr td .image-name img{margin-right:12px}
.appointment{width:90%;padding:25px}.appointment .table-wrapper .table table{background:#fff}.appointment .table-wrapper .table table tbody tr:last-child td{border-bottom:1px solid #dbdde0}.appointment .table-wrapper .table-buttons{padding:24px;display:-webkit-flex;display:flex}.appointment .table-wrapper .table-buttons button{background:none;border:none;outline:1px solid #e8e8e8;border-radius:4px;padding:7px 11px;margin:4px;cursor:pointer}.appointment .table-wrapper .table-buttons button:disabled{opacity:.7}.appointment .table-wrapper .table-buttons button:active,.appointment .table-wrapper .table-buttons button:focus{background:#336cfb;color:#fff}.appointment .icon img{position:fixed;right:0;bottom:0;margin:24px;cursor:pointer}
@media only screen and (max-width: 1024px){.Sidebar{display:-webkit-flex;display:flex;-webkit-justify-content:initial;justify-content:initial;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;width:10%}.Sidebar .logo img:first-child{display:none}.Sidebar .logo img:last-child{display:block}.Sidebar p{display:none}.Sidebar nav li a{margin-left:0px}.Sidebar nav li a span{display:none}.search-and-notification .search{width:85%}.search-and-notification .notification-profile{width:20%}.cards{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr;place-items:center}.cards .card-lists{width:95%;margin:10px 0}.cards .card-lists .info-and-number{margin-left:10px}.little-charts-container{-webkit-flex-direction:column;flex-direction:column}.little-charts-container .little-chart{width:100%}.table{overflow:scroll}}@media only screen and (max-width: 600px){body .search-and-notification .notification-profile{width:30%}}@media only screen and (max-width: 450px){body .appointment{padding:10px}body .cards{display:grid;grid-template-columns:1fr;grid-template-rows:repeat(4, 1fr)}body .cards .card-lists{width:100%}body .search-and-notification{background:#fff;box-shadow:0px 4px 16px rgba(16,30,115,.08)}body .search-and-notification .notification-profile{width:35%}body .search-and-notification .hamburger-menu{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;flex-direction:column;margin-left:16px}body .search-and-notification .hamburger-rotate .hamburger-line:first-child{transition:.5s;-webkit-transform:rotate(-45deg) translate(-2px, 2px);transform:rotate(-45deg) translate(-2px, 2px)}body .search-and-notification .hamburger-rotate .hamburger-line:nth-child(2){opacity:0}body .search-and-notification .hamburger-rotate .hamburger-line:last-child{transition:.5s;-webkit-transform:rotate(45deg) translate(-8px, -10px);transform:rotate(45deg) translate(-8px, -10px)}body .search-and-notification .search{display:none}body .search-and-notification .search img{margin-left:50px}body .Sidebar.active{left:0;width:100%;height:100vh;z-index:1111}body .Sidebar{transition:.7s;position:fixed;top:0;left:-100%}body .Sidebar .search{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}body .Sidebar .logo img:first-child{display:block}body .Sidebar .logo img:last-child{display:none}body .Sidebar li{padding:5px}body .Sidebar li a{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}body .Sidebar li a span{display:block}body .table-wrapper table{overflow:scroll}}@media only screen and (max-width: 350px){body .search-and-notification .hamburger-menu{margin-left:7px;-webkit-align-items:flex-start;align-items:flex-start}body .search-and-notification .hamburger-menu .hamburger-line{width:20px}body .search-and-notification .notification-profile{width:50%}}
